import Vue from "vue";
import VueRouter from "vue-router";

import App from "./App.vue";

Vue.config.productionTip = false;
Vue.use(VueRouter);

console.log(process.env.VUE_APP_API_URL);

const router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/"
    },
    {
      path: "/project/:id"
    },
    {
      path: "/category/:id"
    }
  ]
});

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");
